.mew-tooltip>div{display:inline-block}.tippy-tooltip.light-theme{background-color:#fff;color:initial;-webkit-box-shadow:0px 10px 20px rgba(0,0,0,.08);box-shadow:0px 10px 20px rgba(0,0,0,.08);border:1px solid #dadada;padding:10px}.tippy-tooltip.light-theme .tippy-arrow{border-top-color:#fff}.tippy-tooltip.light-theme .tippy-arrow::after{content:"";position:absolute;z-index:-1;border-color:transparent;border-style:solid;border-top-color:#dadada;border-width:7px 7px 0;top:-6px;left:-7px}.tippy-tooltip.light-theme .tippy-content{word-break:break-all}.tippy-tooltip.light-theme .tippy-content{word-break:break-word !important;text-align:left !important}